import axios from 'axios'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    methods: {
        getData() {
            axios.get(`/api/member/getMemberInfo/${this.getLogin.user.id}`)
                .then(res => {
                    this.id = this.getLogin.user.id;
                    this.name = res.data.name;
                    this.zipcode = res.data.zipcode;
                    this.addr1 = res.data.addr1;
                    this.addr2 = res.data.addr2;
                    this.tel = res.data.tel;
                    this.email = res.data.email;
                })
        },
        update() {
            let member;
            if (this.pwd1 != null)
                if (this.pwd1 == this.pwd2) {
                    member = {
                        id: this.id,
                        password: this.pwd1,
                        name: this.name,
                        zipcode: this.zipcode,
                        addr1: this.addr1,
                        addr2: this.addr2,
                        tel: this.tel,
                        email: this.email,
                    }
                }
            else {
                member = {
                    id: this.id,
                    name: this.name,
                    zipcode: this.zipcode,
                    addr1: this.addr1,
                    addr2: this.addr2,
                    tel: this.tel,
                    email: this.email,
                }
            }
            console.log(member)
            axios.put('/api/member/updateMember', member)
                .then(() => {
                    alert("수정 성공")
                    this.getData();
                })
                .catch(() => alert("수정 실패"))
        },
        secession() {
            axios.get('/api/member/login', {
                    params: {
                        id: this.id,
                        password: this.password,
                    }
                })
                .then(() => {
                    axios.delete(`/api/member/delete/${this.id}`)
                        .then(() => {
                            alert("탈퇴가 완료되었습니다.");
                            this.$router.push('/')
                        })
                        .catch(() => {
                            alert("배송중인 상품이 있어 탈퇴에 실패했습니다.");
                        })
                })
                .catch(() => {
                    alert("비밀번호가 일치하지 않습니다.")

                })
                .finally(this.$refs.password.reset())
        },

    },
    components: {},
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    mounted() {
        console.log(this.getLogin)
        if (this.getLogin) {
            this.getData();
        }
    },
    data() {
        return {
            dialog: false,
            id: '',
            pwd1: '',
            pwd2: '',
            name: '',
            zipcode: '',
            addr1: '',
            addr2: '',
            tel: '',
            email: '',
            password: '',
        }
    }
}
