import axios from 'axios'
import FAQTypeDisplay from '@/components/FAQTypeDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        FAQTypeDisplay,
    },
    data() {
        return {
            totalContents: 0,
            contents: [],
            options: {
                itemsPerPage: 50,
            },
            loading: true,
            expanded: [],
            admin: true,
            headers: [{
                    text: '',
                    value: 'icon',
                    width: '5%',
                    align: 'center',
                }, {
                    text: '종류',
                    value: 'type',
                    width: '12%',
                    align: 'center',
                },
                {
                    text: '제목',
                    value: 'title',
                    width: '83%',
                    align: 'center',
                },
                {
                    text: '',
                    value: 'data-table-expand'
                },
            ],
            search: 'all',

        }
    },
    methods: {
        colorPicker(put) {
            if (this.search == put) {
                return 'secondary'
            }
        },
        getFAQ(selectedType) {
            this.loading = true
            this.search = selectedType;
            this.expanded = [];
            axios.get(`/api/faq/get/${selectedType}`)
                .then(res => {
                    this.contents = res.data;
                    this.loading = false
                })
        },
        deleteFAQ(num) {
            axios.delete(`/api/faq/deletefaq/${num}`)
                .then(() => {
                    alert("삭제가 완료되었습니다.")
                    this.$router.go();
                })

        },
        updateFAQ(num) {
            this.$router.push(`/updatePost/faq/${num}`);

        }
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getFAQ('all')
            },
            deep: true,
        },
    },
}
