import axios from 'axios'
import HideId from '@/components/HideId.vue'
import DateDisplay from '@/components/DateDisplay.vue'
import QnATitleDisplay from '@/components/QnATitleDisplay.vue'
import {
    createNamespacedHelpers
} from 'vuex'
const LoginStore = createNamespacedHelpers('LoginStore')
export default {
    components: {
        HideId,
        DateDisplay,
        QnATitleDisplay,
    },
    data() {
        return {
            totalContents: 0,
            contents: [],
            options: {},
            loading: true,
            headers: [{
                    text: '번호',
                    value: 'qnaNo',
                    width: '10%',
                    align: 'center',
                    divider: true,
                },
                {
                    text: '상품명',
                    value: 'productName',
                    width: '20%',
                    align: 'center',
                    divider: true
                },
                {
                    text: '제목',
                    value: 'type',
                    width: '45%',
                    align: 'center',
                    divider: true
                },
                {
                    text: '작성자',
                    value: 'id',
                    width: '10%',
                    align: 'center',
                    divider: true
                },
                {
                    text: '작성일',
                    value: 'regDate',
                    width: '15%',
                    align: 'center',
                },
            ],
            searches: [{
                text: '상품명',
                value: 'productName'
            }, {
                text: '작성자',
                value: 'id'
            }],
            search: 'id',
            searchWord: '',

        }
    },
    methods: {
        getQnA() {
            this.loading = true;
            const {
                page,
                itemsPerPage
            } = this.options
            let link = document.location.href;
            link = link.slice(26, link.length - 3);
            axios.get(`/api/qna/getQnaListByType`, {
                params: {
                    page: page,
                    perPage: itemsPerPage,
                    search: this.search,
                    searchWord: this.searchWord,
                    type: link
                }
            }).then(res => {
                this.contents = res.data.qnaList;
                this.totalContents = res.data.count;
            }).finally(() => {
                this.loading = false;
            })
        },
        moveto(item) {
            if (item.secret == true) {
                if (this.getLogin == null || this.getLogin.user.id != item.id && this.getLogin.user.authority != 'ROLE_ADMIN') {
                    alert('비밀글입니다');
                    return;
                }
            }
            this.$router.push(`/qna/${item.qnaNo}`)
        },
    },
    computed: {
        ...LoginStore.mapGetters(['getLogin']),
    },
    watch: {
        options: {
            handler() {
                this.getQnA()
            },
            deep: true,
        },
    },
}
